<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <div slot="header">
          <h5 class="title">ADD TENANT</h5>
        </div>
        <form>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">NAME</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="tenant.name"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">IC</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="tenant.ic"></fg-input>
            </div>
            <label class="col-md-2 col-form-label text-right">CONTACT</label>
            <div class="col-md-2">
              <fg-input type="text" v-model="tenant.contact"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">EMAIL</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="tenant.email"></fg-input>
            </div>
          </div>
          <hr>
          <div slot="header">
            <h5 class="title">ADDITIONAL INFO</h5>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">EMERGENCY CONTACT</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="tenant.emergencyContact"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">EMERGENCY NAME</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="tenant.emergencyName"></fg-input>
            </div>
          </div>
          <div class="row">
            <div class="col-md-6 offset-md-4">
              <n-button class="button" type="info" @click.native="post(); handleAdd();">ADD</n-button>
              <!-- <n-button class="button" type="danger">CANCEL</n-button> -->
            </div>
          </div>
        </form>
      </card>
    </div>
  </div>
</template>
<script>
import { Radio } from "src/components";
import { Switch } from "src/components";
import swal from "sweetalert2";

export default {
  components: {
    [Radio.name]: Radio,
    [Switch.name]: Switch
  },
  data() {
    return {
      tenant: {
        name: "",
        ic: "",
        email: "",
        contact: "",
        emergencyContact: "",
        emergencyName: "",
        status: "ACTIVE"
      },
      switches: {
        defaultOn: true
      }
    };
  },
  methods: {
    post() {
      this.$http.post(
        "https://admindashboard-b2ab9.firebaseio.com/tenant.json",
        this.tenant
      );
    },
    handleAdd() {
      swal({
        title: `You added ${this.tenant.name}`,
        buttonsStyling: false,
        type: "success",
        confirmButtonClass: "btn btn-success btn-fill",
        onClose: () =>{
            window.location.href = `#/tenant/tenantlist`;
          }
      });
    }
  }
};
</script>
<style scoped>
.display-inline {
  display: inline !important;
}
</style>
